// Background image removal
html, .navbar, .pagnation.postpagnation, .forumcategoryheader, .forumpanel{
	background-image: none;
}

// Main forum background
#wrapper {
	color: @fp-text-normal;
	//Blame Firefox
	@temp1: escape(%("%a", @fp-wrapper));
	@temp2: escape(%("%a", @fp-wrapper-light));
	background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="100" height="100" viewBox="0 0 100 100"><path d="m 0,0 100,0 0,100 -100,0 z" fill="@{temp1}" stroke="black" stroke-width="0" /><path d="M 0,50.5 49.5,100 99,100 0,1 Z" fill="@{temp2}" stroke="black" stroke-width="0" /><path d="m 50,0 50,50 0,-50 z" fill="@{temp2}" stroke="black" stroke-width="0" /></svg>') repeat;
}

// Buttons
.button {
	background-color: @fp-background-grey-normal;
	border: 1px solid #aaa;
	color: @fp-text-normal;
	float: right;
	font-weight: 700;
	padding: 2px 10px;
    margin: 0rem 0.2rem 0.8rem  0.1rem;
    font-size: 0.9rem;
	&:active {
		color: @fp-text-normal;
	}
	&:hover {
		color: @fp-text-light;
		background: @fp-background-grey-light;
	}
	.button {
		&.is-info {
			margin: 0rem 1rem;
			font-size: 1rem;
		}
	}
	&.is-primary {
		background: @fp-background-blue-normal;
		margin-left: 0.2rem;
		height: 2rem;
		&:hover {
			background: @fp-background-blue-light;
		}
	}
	&.is-primary[disabled] {
		.button.is-primary;
	}
	&.is-success, &.is-success.is-outlined  {
		background-color: @fp-background-blue-normal;
		border-color: @fp-background-blue-normal;
		color: @fp-text-lighter;
		&:hover {
			.button.is-success.is-hovered;
		}
		&:focused {
			.button.is-success.is-hovered;
		}
		&.is-hovered {
			background-color: @fp-background-blue-light;
			border-color: @fp-background-blue-light;
			color: @fp-text-lighter;
		}
	}
	&.is-warning, &.is-warning.is-outlined  {
		background-color: @fp-wrapper;
		border-color: @fp-wrapper;
		color: @fp-text-lighter;
		&:hover {
			.button.is-warning.is-hovered;
		}
		&:focused {
			.button.is-warning.is-hovered;
		}
		&.is-hovered {
			background-color: @fp-background-grey-dark;
			border-color: @fp-background-grey-dark;
			color: @fp-text-lighter;
		}
	}
}
// Footer
.footer {
	background: @fp-background-blue-normal;
	a {
		color: @fp-text-lighter;
	}
}
body {
	.force-wide-design {
		.section {
 			padding: 1rem 0rem;
 		}
	}
	&.page-forum {
		.forumhead {
			padding: 1rem 0rem 0rem 1rem;
		}
	}
}
.section {
    padding: 3rem 0rem;
}

.title {
	color: @fp-text-normal;
}
.field.has-addons .control .input, body.page-forum .threadpost input {
		background: @fp-background-white-normal;
		color: @fp-text-normal;
}
.box {
	background-color: @fp-background-white-normal;
	color: @fp-text-normal;
	margin-top: 1rem;
	margin-bottom: 1rem;
}

.common-link {
	 a {
		color: @fp-text-link-normal;
		&:hover {
			color: @fp-text-link-light;
		}
	}
}
.common-link;
strong {
	color: @fp-text-link-normal;
}
.force-wide-design {
	.container {
    	width: calc(100% - 1.9rem) !important;
	}
}

.hero {
	&-body {
		padding: 0.5rem 0rem 0.5rem 0rem;
		color: @fp-text-normal;
		background-color: @fp-background-white-normal !important;
		.container {
			padding-top: 0em;
			color: @fp-text-normal;
			background-color: @fp-background-white-normal;
			padding: 3rem 1.5rem;
		}
		h2 {
			color: @fp-text-normal;
		}
	}
	&.is-dark {
		.title {
			color: @fp-text-normal;
		}
		.subtitle{
			color: @fp-text-normal;
			strong {
				color: @fp-text-light;
			}
		}
	}
	&.is-danger {
		background-color: @fp-background-white-normal;
		color: @fp-text-normal;
		.hero.is-dark;
	}
}